import { SettingStore } from '../stores';
class Engine {
   // constructor(prop) {}

   popupOn = (popup)=> {
        SettingStore.dispatch({type:'popupTrigger', popup:popup});
    }
    popupOff = (popup)=> {
        SettingStore.dispatch({type:'popupTrigger', popup: false});
    }
    loadingOn = ()=> {
        SettingStore.dispatch({type:'addLoading', id: this.id});
    }
    loadingOff = ()=> {
        SettingStore.dispatch({type:'removeLoading', id: this.id});
    }

    get = (uri, callback)=>{
      // const me = this;
      fetch(uri, {
          method: 'GET'
          }
      ).then(v=>{
          return v.json()
      }).then((data)=> {    
          callback(data);
      }).catch((err) => {
          callback({status:'failure', message:err.message});
      });
  }
  post = (uri, data, auth, callback)=>{
    //const me = this;
    fetch(uri, {
        method: 'POST',
        data : data,
        herder: {
                    
        }
    }
    ).then(v=>{
        return v.json()
    }).then((data)=> {    
        callback(data);
    }).catch((err) => {
        callback({status:'failure', message:err.message});
    });
}


    getContent = (uri,  callback)=>{
        // const me = this;
        fetch(uri, {
            method: 'GET',
        }).then(v=>{
            return v.text()
        }).then((data)=> {
            callback(data);
        }).catch((err) => {
            callback('err');
        });
    }
}

export default Engine;