import React, {  useEffect, useState }  from 'react';
import { Container } from 'react-bootstrap';
import { Banner  } from '../modules';
import { useUIComponents  } from '../customHooks';

import { Engine  } from '../comm';
/*
import { Home, Banner, Header, Body, WalletAccount, TokenDetails,  TokensList, TrackFrom, MintFrom, NoExist, ContractAdmin  } from '../modules';
import { useWeb3Log, useFrameContainer } from "../customHooks";
import ContractProjects from './ContractProjects';
*/
export default (props)=> {
  const eng = new Engine();

  const { centrelBox, headerBox, cardListShow } = useUIComponents();
  const [ prodList , setProdList ] = useState([]);
  
  const loadProducts = ()=>{
    eng.get('/metaData/products.json', v=>{
      setProdList(v);
    });
  }
  useEffect(()=>{
    loadProducts();
  }, [])

  return (
    <Container fluid={true}  className="p-0 text-left" style={{minHeight:'58rem'}}>
      {centrelBox(<Banner/>)}
      {headerBox()}
      <Container fluid={true} className="p-3 home-bg">
        <Container fluid={true} className="p-3 text-center text-secondary" style={{minHeight:'36rem'}}>
        <Container className="p-3">
            <h3>
                Universal Stamps(UNST) is a cross chain ERC-1155 Solution.
            </h3>
        </Container>
        { cardListShow(prodList)}
        </Container>
    </Container>
    </Container>

  );
}